@font-face {
    src: url("./Inter-Regular.woff2") format("woff2"),
        url("./Inter-Regular.woff") format("woff");
    font-family: "Inter";
    font-style:  normal;
    font-weight: normal;
    font-display: swap;
}

@font-face {
    font-family: 'Inter';
    font-style:  normal;
    font-weight: 500;
    font-display: swap;
    src: url("./Inter-Medium.woff2") format("woff2"),
         url("./Inter-Regular.woff") format("woff");
}

@font-face {
    src: url("./Inter-Bold.woff2") format("woff2"),
        url("./Inter-Bold.woff") format("woff");
    font-family: "Inter";
    font-style:  normal;
    font-weight: bold;
    font-display: swap;
}

@font-face {
    src: url("./Inter-Black.woff2") format("woff2"),
        url("./Inter-Black.woff") format("woff");
    font-family: "Inter";
    font-style:  normal;
    font-weight: 900;
    font-display: swap;
}
